#feedback {
    background-image: url('../../assets/image/feedback-bg.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    padding: 100px 0 120px 0;
}

#feedback h3 {
    color: #fff;
    font-size: 40px;
}

.feedback__form-box {
    display: flex;
    justify-content: center;
}

@media (max-width: 426px) {
    #feedback {
        padding: 50px 0;
    }
    #feedback h3 {
        font-size: 20px;
    }
}