.feedback__form {
    display: flex;
    flex-direction: column;
    row-gap: 16px;
    max-width: 530px;
    width: 100%;
}

.feedback__form input, .feedback__form textarea {
    outline: none;
    font-family: 'Lato', sans-serif;
    font-size: 20px;
    padding: 9px 17px;
    background-color: rgba(207, 207, 207, 0.05);
    backdrop-filter: blur(7px);
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 12px;
    color: #fff;
}
.feedback__form textarea {
    height: 150px;
    resize: none;
}

.feedback__form button {
    margin-top: 17px;
    font-family: 'Lato', sans-serif;
    font-weight: 700;
    color: #fff;
    padding: 17px 10px;
    background: linear-gradient(287.54deg, #D97811 48.07%, #ECEA7D 90.42%, #F4CE4F 94.67%);
    border: none;
    border-radius: 30px;
    opacity: 0.7;
    transition: .3s;
}

.feedback__form button:hover {
    opacity: 1;
}