.lang-button {
    position: absolute;
    right: 10%;
    top: 60px;
}

.lang-button ul {
    list-style-type: none;
    color: #fff;
}

.fr12 {
    display: none;
}

.MuiInputBase-root {
    color: #fff!important;
    border-radius: 8px!important;
    border: 1px solid #fff!important;
    height: 51px;
    width: 163px;
    font-size: 16px!important;
}

.MuiPaper-root {
    background-color: #111518!important;
    color: #fff!important;
    border: 1px solid #fff!important;
    border-radius: 8px!important;
    font-size: 14px!important;
}
.MuiSvgIcon-root {
    color: #fff!important;
}

.MuiOutlinedInput-notchedOutline {
    border: none!important;
}

@media (max-width: 1024px) {
    .lang-button {
        position: relative;
        right: auto;
        top: auto;
    }

    .MuiInputBase-root {
        width: 100px;
    }
}