#coin-buy {
    position: relative;
    padding-bottom: 40px;
}

#coin-buy h3 {
    padding: 90px 0;
}

.coin-buy__row {
    display: flex;
    flex-direction: column;
    row-gap: 150px;
}

.coin-buy__box {
    display: flex;
    column-gap: 5px;
}

.coin-buy__box h4 {
    font-weight: 800;
    font-size: 240px;
    line-height: 200px;
    text-align: center;
    width: 137px;

    background: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    opacity: 0.2;
}

.coin-buy__box ul {
    list-style-type: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    row-gap: 20px;
    padding: 15px 0;

    font-family: 'Lato', sans-serif;
    font-weight: 700;
    font-size: 25px;
    line-height: 33px;
    color: #ffffff;
}

.coin-buy__box-token span {
    font-family: 'Lato', sans-serif;
    font-size: 25px;
    color: #ffffff;
}

.coin-buy__box-token p {
    margin-top: 35px;
    font-family: 'Lato', sans-serif;
    font-weight: 600;
    font-size: 35px;
    color: #ffffff;
    padding: 20px 30px;
    border: 1px solid rgba(255, 255, 255, 0.5);
    border-radius: 12px;
    background: rgba(207, 207, 207, 0.05);
    backdrop-filter: blur(7px);
}


#coin-buy .coin-buy__img {
    position: absolute;
    top: 100px;
    right: 30px;
}

#coin-buy:after {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 500px;
    height: 250px;
    top: 100px;
    right: 0;
    background: linear-gradient(287.14deg, #F0902A 14.08%, #EBC057 27.89%, #ECEA7D 34.83%, #F0902A 54.33%);
    opacity: 0.5;
    filter: blur(212px);
}

@media (max-width: 1025px) {
    .coin-buy__img, #coin-buy:after {
        display: none;
    }

    .coin-buy__box-token p {
        font-size: 20px;
        padding: 12px 5px;
        text-align: center;
    }

    .coin-buy__box {
        flex-direction: column;
    }
}

@media (max-width: 426px) {
    .coin-buy__img, #coin-buy:after {
        display: none;
    }

    .coin-buy__row {
        row-gap: 50px;
    }

    .coin-buy__box {
        flex-direction: column;
    }

    .coin-buy__box h4 {
        text-align: left;
        font-size: 100px;
        line-height: 90px;
        width: fit-content;
    }

    .coin-buy__box-token {
        padding-top: 40px;
    }

    .coin-buy__box-token p {
        font-size: 13px;
    }
}