header {
    padding: 50px 0 100px 0;
}

.header-top {
    display: flex;
    justify-content: center;
    padding-bottom: 40px;

    position: relative;
}

.header-top p {
    font-family: 'Poppins', sans-serif;
    text-transform: uppercase;
    font-weight: 700;
    font-size: 24px;
    color: #fff;
    display: none;
}
.header-top p span {
    color: transparent;
    -webkit-background-clip: text;
    background-image: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
}



.burger-menu {
    display: none;
}

.burger-btn {
    width: 24px;
    height: 20px;
    display: flex;
    align-items: center;
    cursor: pointer;
    position: relative;
}

.burger-btn span{
    width: 100%;
    height: 3px;
    border-radius: 10px;
    display: block;
    background-color: #ffffff;
    position: relative;
    transition: .5s;
}

.burger-btn span:before, .burger-btn span:after{
    position: absolute;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 10px;
    background-color: #ffffff;
    transition: .5s;
}

.burger-btn span:before{
    top: -8px;
}

.burger-btn span:after{
    top: 8px;
}

.show-btn span {
    transition: .5s;
    transform: rotate(45deg);
}


.show-btn span:before, .show-btn span:after {
    transform: rotate(90deg);
    top: 0;
    bottom: 0;
    transition: .5s;
}

@media screen and (max-width: 1024px) {
    .burger-menu, .header-top p {
        display: block;
    }
}

@media (max-width: 1024px) {
    header {
        padding: 35px 0 0 0;
    }
    .header-top {
        justify-content: space-between;
        align-items: center;
        column-gap: 10px;
        max-width: 85%;
        margin: 0 auto;
    }

    .header-top img {
        display: none;
    }
}