@import url('https://fonts.googleapis.com/css2?family=Lato:wght@400;700&family=Montserrat:wght@500&family=Poppins:wght@700;800&display=swap');

*{
    padding: 0;
    margin: 0;
    box-sizing: border-box;
}

body {
    background-color:  #111518;
    overflow-x: hidden;
}

h1, h2, h3, h4, h5, h6 {
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
}

.container {
    max-width: 85%;
    margin: 0 auto;
}

.row {
    display: flex;
    flex-wrap: wrap;
    margin: 0 -15px;
    row-gap: 30px;
}

.col-2 {
    width: 50%;
    padding: 0 15px;
}

.section__title {
    font-size: 40px;
    color: #ffffff;
    padding-bottom: 35px;
}

@media (max-width: 426px) {
    .col-2 {
        width: 100%;
    }
}

@media (min-width: 1700px) {
    .container {
        max-width: 1300px;
    }
}