#about {
    padding: 80px 0;
    /*overflow-x: hidden;*/
    position: relative;
}

#about .container {
    background-image: url('../../assets/image/about-bg.png');
    background-repeat: no-repeat;
    background-position: 100% 0;
}

#about h3 {
    font-size: 40px;
    color: transparent;
    -webkit-background-clip: text;
    background-image: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
}

#about .container .about__box:nth-child(2) {
    width: 60%;
    position: relative;
}
#about:before {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 215px;
    height: 215px;
    top: -20px;
    right: 0;
    background: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
    filter: blur(212px);
}
#about .container .about__box:nth-child(2):after {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 316px;
    height: 246px;
    top: 250px;
    left: -144px;
    background: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
    filter: blur(212px);
}

.about__box {
    font-family: 'Lato', sans-serif;
    font-size: 22px;
    font-weight: 400;
    line-height: 33px;
    color: #ffffff;
    padding: 40px 0;
}

.about__media-box {
    display: flex;
    justify-content: center;
    padding-top: 50px;
}

@media (max-width: 426px) {
    #about {
        padding: 50px 0;
    }
    #about:before{
        display: none;
    }
    #about .container {
        background-position: 0 35%;
        background-size: 100%;
    }

    #about h3 {
        font-size: 30px;
        text-align: center;
    }
    
    #about .container .about__box:nth-child(2) {
        width: 100%;
    }
    #about .container .about__box:nth-child(2):after {
        display: none;
    }

    .about__box {
        font-size: 20px;
        line-height: 30px;
        padding: 20px 0;
    }


    .about__media-box {
        padding-top: 20px;
    }
    .about__media-box iframe {
        height: 200px;
    }
}