.nav__container {
    width: 85%;
    margin: 0 auto;
}

.nav-list {
    list-style-type: none;
    width: 100%;
    display: flex;
    column-gap: 59px;
    justify-content: center;
    color: #fff;
    font-family: 'Lato', sans-serif;
    font-weight: 700;

    position: relative;
}

.nav-list li {
    cursor: pointer;
}

.nav-list li a{
    color: #fff;
    font-family: 'Lato', sans-serif;
    font-weight: 700;
    text-decoration: none;
}

.active {
    color: #B86B0C!important;
}

.close-btn {
    display: none;
    width: 24px;
    height: 20px;
    /*display: flex;*/
    align-items: center;
    cursor: pointer;
    position: absolute;
    right: 30px;
    top: 30px;
}

.close-btn span{
    width: 100%;
    height: 3px;
    border-radius: 10px;
    display: block;
    background-color: #ffffff;
    transform: rotate(45deg);
    position: relative;
}

.close-btn span:after {
    position: absolute;
    top: 0;
    left: 0;
    content: '';
    width: 100%;
    height: 100%;
    border-radius: 10px;
    transform: rotate(90deg);
    background-color: #ffffff;
}

.back-drop {
    display: none;
}

@media (max-width: 1024px) {

    .nav__container {
        position: absolute;
        top: 0;
        left: 0;
        bottom: 0;
        right: 0;
        width: 100%;
        padding-top: 35px;
        display: flex;
        justify-content: center;
        align-items: flex-start;
    }

    .nav__container__hide {
        display: none;
    }
    .close-btn {
        display: flex;
    }

    .nav-list {
        width: 80%;
        padding: 60px 0 80px 0;
        flex-direction: column;
        row-gap: 24px;
        text-align: center;
        background-color: #121517;
        position: relative;
        z-index:3;
    }

    .back-drop {
        display: block;
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;
        background-color: rgba(9, 9, 9, 0.9);
        z-index: 2;
    }
}
