#game {
    padding-top: 100px;
}

#game span{
    font-family: 'Poppins', sans-serif;
    font-weight: 700;
    font-size: 20px;
    background: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
}

.game__left p, .game__text {
    font-family: 'Lato', sans-serif;
    font-size: 25px;
    line-height: 32px;
    color: #ffffff;
    padding: 20px 0;
}

.game__row {
    position: relative;
}
.game__left {
    max-width: 40%;
}

.game__right {
    position: absolute;
    top: 0;
    right: 0;
    z-index: -1;
}

.game__right img {
    padding: 30px 10px;
}

.disclaimer {
    margin-top: 150px;
    padding-bottom: 100px;
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    border-bottom: 1px solid #ffffff;

    background-image: url('../../assets/image/disclaimer-bg.png');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;

}

.disclaimer__title {
    background-color: #111518;
}

.disclaimer h4{
    text-align: center;
    font-size: 35px;
    background: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
    -webkit-background-clip: text;
    -webkit-text-fill-color: transparent;
    padding: 20px 40px;
    width: fit-content;
}

.disclaimer h4:after {
    content: "";
    position: absolute;
    top: 45px;
    left: 0;
    width: 100%;
    height: 1px;
    background: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
    z-index: -1;
}

.disclaimer p {
    font-family: 'Lato', sans-serif;
    color: #ffffff;
    line-height: 29px;
    text-align: center;
    font-size: 22px;
}

@media (max-width: 1025px) {
    .game__row .container {
        display: flex;
        flex-direction: column-reverse;
    }
    .game__left {
        max-width: 100%;
    }

    .game__right {
        position: relative;
        top: auto;
        right: auto;
        z-index: 1;
    }

    .game__right img {
        width: 100%;
    }
}

@media (max-width: 426px) {
    .disclaimer {
        margin-top: 0;
        padding-bottom: 50px;
    }
}