#after-buy {
    padding: 100px 0;
    position: relative;
}
#after-buy:after {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 300px;
    height: 250px;
    top: 100px;
    left: -100px;
    background: linear-gradient(287.14deg, #F0902A 14.08%, #EBC057 27.89%, #ECEA7D 34.83%, #F0902A 54.33%);
    opacity: 0.5;
    filter: blur(190px);
    z-index: -1;
}

#after-buy p{
    font-family: 'Lato', sans-serif;
    font-size: 25px;
    line-height: 37px;
    color: #ffffff;
}

.after-buy__row {
    display: flex;
    column-gap: 50px;
}


@media (max-width: 1025px) {
    .after-buy__row {
        flex-direction: column;
        row-gap: 20px;
    }
    .after-buy__box{
        display: flex;
        justify-content: center;
    }

    #after-buy:after {
        display: none;
    }
}

@media (max-width: 510px) {
    .after-buy__box img {
        width: 100%;
    }
}