#cover {
    background-image: url('../../assets/image/cover-bg.jpg');
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
    min-height: 100vh;
}

#cover h1 {
    font-size: 95px;
    color: transparent;
    -webkit-background-clip: text;
    background-image: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
}

.cover__left {
    position: relative;
}
.cover__left:after {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 330px;
    height: 330px;
    top: 200px;
    left: -50%;
    background: #B86B0C;
    opacity: 0.2;
    filter: blur(86px);
}

.cover__right {
    position: relative;
    display: flex;
    justify-content: flex-end;
}

.cover__right img {
    width: 100%;
}

.cover__right:after {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 500px;
    height: 250px;
    top: 100px;
    left: -50px;
    background: linear-gradient(287.14deg, #F0902A 14.08%, #EBC057 27.89%, #ECEA7D 34.83%, #F0902A 54.33%);
    opacity: 0.5;
    filter: blur(212px)
}

@media (max-width: 1220px) {
    #cover h1 {
        font-size: 60px;
    }
}
@media (max-width: 769px) {
    #cover .col-2 {
        width: 100%;
    }
    #cover h1 {
        text-align: center;
    }
}

@media (max-width: 426px) {
    #cover {
        overflow: hidden;
        min-height: auto;
    }
    #cover .row {
        row-gap: 0;
    }

    #cover h1 {
        font-size: 32px;
        text-align: center;
    }

    .cover__left:after {
        width: 0;
        height: 0;
        top: 0;
        left: 0;
    }

    .cover__right:after {
        width: 176px;
        height: 137px;
        top: 100px;
        left: -50px;
        background: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
        opacity: 0.5;
        filter: blur(92px);
    }
    .cover__right:before {
        content: "";
        position: absolute;
        border-radius: 50%;
        width: 176px;
        height: 137px;
        top: 100px;
        right: -50px;
        background: linear-gradient(292.8deg, #F0902A 17.33%, #EBC057 35.17%, #ECEA7D 44.13%, #F0902A 69.31%);
        opacity: 0.5;
        filter: blur(92px);
    }

}