.slider__container {
    max-width: 100%;
    margin: 0 auto;
}

.scrolling-wrapper {
    margin-top: 100px;
    display: flex;
    column-gap: 23px;
    flex-wrap: nowrap;
    overflow-x: auto;
    overflow-y: hidden;
}
.scrolling-wrapper::-webkit-scrollbar {
    width: 0;
}

.card {
    flex: 0 0 auto;
}

.card img {
    width: 100%;
    height: 100%;
}

@media (min-width: 1700px) {
    .slider__container {
        max-width: 1300px;
    }
}

@media (max-width: 425px) {
    .scrolling-wrapper {
        margin-top: 50px;
        height: 65px;
    }
}

