#tocenomika {
    padding: 70px 0;
    position: relative;
    overflow-x: hidden;
}

#tocenomika p{
    font-size: 25px;
    font-weight: 700;
    line-height: 37px;
    color: #ffffff;
}

#tocenomika:after {
    content: "";
    position: absolute;
    border-radius: 50%;
    width: 500px;
    height: 250px;
    top: 0;
    left: -161px;
    background: linear-gradient(287.14deg, #F0902A 14.08%, #EBC057 27.89%, #ECEA7D 34.83%, #F0902A 54.33%);
    opacity: 0.5;
    filter: blur(212px)
}


.tocenomika__box {
    display: flex;
    justify-content: center;
    padding-top: 50px;
}

.mob-diagram {
    display: none;
}

.parent-box {
    position: relative;
}

.parent-box img {
    width: 100%;
    max-width: 1087px;
}

.tocenomika__box span {
    font-family: 'Lato', sans-serif;
    font-size: 25px;
    font-weight: 400;
    position: absolute;
    color: #ffffff;
}

.first-card {
    top: 21%;
    left: 4%;
}

.second-card {
    left: 11%;
    bottom: 17%;
}

.third-card {
    top: 5%;
    right: 18%;
}

.fourth-card {
    right: 3%;
    bottom: 34%;
}

@media (max-width: 426px) {
    #tocenomika:after {
        display: none;
    }

    #tocenomika h3 {
        padding: 0;
    }

    #tocenomika p {
        display: none;
    }

    .mob-diagram {
        display: block;
    }

    .tocenomika__box {
        justify-content: flex-start;
        flex-direction: column;
        padding-top: 0;
    }

    .parent-box img {
        display: none;
    }

    .parent-box {
        display: flex;
        flex-direction: column;
    }

    .tocenomika__box span {
        position: relative;
        left: auto;
        right: auto;
        bottom: auto;
        top: auto;
    }
}